import { connect } from 'react-redux';

import actionCreators from '../../actions/RootModal/ActionCreators';
import ModalRoot from '../../components/ModalRoot/ModalRoot';
import ModalDialogs from '../../ModalDialogs';
import { IApplicationState } from '../../store';

const mapStateToProps = (state: IApplicationState) => {
    const modal = ModalDialogs.Get(state.rootModalDialogState.modalType);
    return {
        modal,
        modalProps: state.rootModalDialogState.modalProps,
        title: state.rootModalDialogState.modalTitle
    }
}

const RootModalContainer = connect(
    mapStateToProps, { onClose: actionCreators.
        CloseModalDialog.bind(actionCreators) })(ModalRoot);

export default RootModalContainer;



