import './styles.css';

import * as React from 'react';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';

export default class TaskEvent extends React.PureComponent<ITaskViewProps> {
    public render() {
        return <div className="calendar-event-task bg-info">
            <TaskEditLinkTitle
                task={this.props.task}
                onClick={this.props.onEdit} />
            {this.props.task.assignedToStr}
            <br />
            <a target='_blank' href={this.props.task.projectSiteUrl}>
                {this.props.task.deltekProjectNumber}
            </a>
        </div>;
    }
}


