import './styles.css';

import * as React from 'react';

import { IMilestoneEventProps } from './MilestoneEventProps';

export default class MilestoneEvent extends React.PureComponent<IMilestoneEventProps> {
    public render() {
        return <div className="calendar-event-milestone bg-danger">
            <a target='_blank' href={this.props.milestone.titleUrl}>
                {this.props.milestone.title}
            </a>
            <br />
            {this.props.milestone.responsible !== null ? this.props.milestone.responsible.displayName : ''}
            <br />
            <a target='_blank' href={this.props.milestone.projectSiteUrl}>
                {this.props.milestone.deltekProjectNumber}
            </a>
        </div>;
    }
}


