import { connect } from 'react-redux';

import AADUserInfo from '../../components/Users/AADUserInfo/AADUserInfo';
import { IApplicationState } from '../../store';

function mapStateToProps(state: IApplicationState) {
    return {
        user: state.common.loggedUser
    }
}

const LoggedUserInfoContainer = connect(
    mapStateToProps, {})(AADUserInfo);
export default LoggedUserInfoContainer;


