import * as React from 'react';
import { ListGroupItem } from 'react-bootstrap';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';
import TaskLinks from '../TaskLinks/TaskLinks';

export default class TaskMobile extends React.PureComponent<ITaskViewProps> {
    public render() {
        return <ListGroupItem key={this.props.task.id}>
            <TaskEditLinkTitle
                task={this.props.task}
                onClick={this.props.onEdit} />

            <a href={this.props.task.projectSiteUrl}
                target="_blank"
                className="btn-link">{this.props.task.deltekProjectNumber}</a>
                <br />
                <br />
            <strong>Assigned To: </strong>{this.props.task.assignedToStr}
                <br />
            <strong>Assigned By: </strong>{this.props.task.assignedByStr}
                <br />
            <strong>Status: </strong>{this.props.task.status}
                <br />
            <strong>Due: </strong>{this.props.task.dueLocalStr}
                <br /><br />
            <TaskLinks task={this.props.task} />
        </ListGroupItem>
    }
}


