import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';

import { IModalRootProps } from './ModalRootProps';

export default class ModalRoot extends React.PureComponent<IModalRootProps> {
    public render() {
        if (this.props.modal !== null) {
            return <Modal backdrop="static" show={true} onHide={this.props.onClose}>
                <Modal.Header>
                    <Modal.Title>
                        {this.props.title}
                        <Button className="pull-right" bsStyle="warning" onClick={this.props.onClose}>Close</Button>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <this.props.modal
                        {...this.props.modalProps} />
                </Modal.Body>
            </Modal>
        } return null;
    }
}


