import { connect } from 'react-redux';
import actionCreators from '../../actions/RootModal/ActionCreators';
import ProjectCard from '../../components/Projects/ProjectCard/ProjectCard'
import Project from '../../models/Project';
import { IApplicationState } from '../../store';

function mapStateToProps(state: IApplicationState) {
    return {
        project: state.projectsDashboard.project,
        roles: state.projectsDashboard.roles,
    }
}

function mapDispatchToProps(dispatch: any) {
    return {
        onAddModules: (project: Project) =>
            dispatch(actionCreators.OpenModalDialog('ADD_MODULES', { project }, 'Add Modules')),
        onAddSingleTask: (project: Project) =>
            dispatch(actionCreators.OpenModalDialog('ADD_SINGLE_TASK', { project }, 'Add Single Task'))
    }
}

const ProjectInfoContainer = connect(
    mapStateToProps, mapDispatchToProps)(ProjectCard);
export default ProjectInfoContainer;


