import * as React from 'react';

import { IProjectFullInfoProps } from './ProjectFullInfoProps';

export default class ProjectFullInfo extends React.PureComponent<IProjectFullInfoProps> {
    public render() {
        return <div className="card">
            <div className="card-body">
                <a target="_blank" href={this.props.project.siteUrl}>
                    <h4 className="card-title">{this.props.project.projectName}</h4>
                </a>
                <h5 className="card-subtitle mb-2 text-muted">{this.props.project.deltekProjectNumber}</h5>
                <p className="card-text">
                    <b>Principal:</b>
                    {this.props.project.principal !== null ? this.props.project.principal.displayName : ''}
                </p>
                <p className="card-text">
                    <b>Project Manager:</b>
                    {this.props.project.projectManager !== null ? this.props.project.projectManager.displayName : ''}
                </p>
                <p className="card-text"><b>Profit Center:</b> {this.props.project.profitCenter}</p>
            </div>
        </div>
    }
}




