import * as React from 'react';

import { ITaskViewProps } from '../ITaskViewProps';
import TaskEditLinkTitle from '../TaskEditLinkTitle/TaskEditLinkTitle';
import TaskLinks from '../TaskLinks/TaskLinks';

export default class TaskDesktop extends React.PureComponent<ITaskViewProps> {
    public render() {
        return <tr key={this.props.task.id}>
            <td>
                <TaskEditLinkTitle
                    task={this.props.task}
                    onClick={this.props.onEdit} />

                <a href={this.props.task.projectSiteUrl}
                    target="_blank"
                    className="btn-link">{this.props.task.deltekProjectNumber}</a>
            </td>
            <td>{this.props.task.assignedToStr}</td>
            <td>{this.props.task.assignedByStr}</td>
            <td>{this.props.task.status}</td>
            <td>{this.props.task.dueLocalStr}</td>
            <td>
                <TaskLinks task={this.props.task} />
            </td>
        </tr>
    }
}


