import * as React from 'react';
import { Button } from 'react-bootstrap';

import { ILogOutProps } from './LogOutProps';

export default class LogOut extends React.PureComponent<ILogOutProps> {
    public render() {
        return <Button
            onClick={this.props.onLogOut}
            bsStyle="primary"
            bsSize="small">Log out</Button>;
    }
}


