import * as React from 'react';
import { Modal, ProgressBar } from 'react-bootstrap';

import { IProgressProps } from './ProgressProps';

export default class Progress extends React.PureComponent<IProgressProps> {
    constructor(props: IProgressProps) {
        super(props);
        this.onHide = this.onHide.bind(this);
    }
    
    public render() {
        return <Modal keyboard={false} backdrop={true} onHide={this.onHide} show={this.props.show}>
            <Modal.Header>
                <Modal.Title>Loading...</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <ProgressBar active={true} striped={true} bsStyle="info" now={100} />
            </Modal.Body>
        </Modal>
    }
    
    private onHide() {;}
}


