import * as React from 'react';

import { IProjectShortInfoProps } from './ProjectShortInfoProps';

export default class ProjectShortInfo extends React.PureComponent<IProjectShortInfoProps> {
    public render() {
        return <div>
            <h5 className="mb-1">
                {this.props.item.projectName}
            </h5>
            <h6 className="mb-1">
                <strong>{this.props.item.deltekProjectNumber} </strong>
            </h6>
        </div>;
    }
}


