import * as React from "react";
import { IAADUserInfoProps } from "./AADUserInfoProps";

export default class AADUserInfo extends React.PureComponent<IAADUserInfoProps> {
    public render() {
        if (this.props.user) {
            return <span>
                <strong>{this.props.user.displayName}</strong>
            </span>;
        }
        return null;
    }
}


